<template>
  <div id="app">
    <CallControls @makeCall="makeCall" @endCall="endCall" />
    <CallLog :callHistory="callHistory" />
    <CallSummary :callStatus="callStatus" />
  </div>
</template>

<script>
import CallControls from './components/CallControls.vue';
import CallLog from './components/CallLog.vue';
import CallSummary from './components/CallSummary.vue';
import SIP from 'sip.js';

export default {
  components: {
    CallControls,
    CallLog,
    CallSummary
  },
  data() {
    return {
      callHistory: [],
      callStatus: '',
      sipUserAgent: null
    };
  },
  methods: {
    initSIP() {
      this.sipUserAgent = new SIP.UserAgent({
        uri: 'sip:101@sip.xtrevo.com',
        transportOptions: {
          wsServers: ['wss://sip.xtrevo.com:8443/asterisk-wss'],
        },
        authorizationUser: '101',
        password: 'severalleddepend8912@@',
      });

      this.sipUserAgent.start().catch(error => {
        console.error('Failed to start SIP User Agent:', error);
      });
    },
    makeCall(number) {
      const session = this.sipUserAgent.invite(`sip:${number}@sip.xtrevo.com`);

      session.on('accepted', () => {
        this.callStatus = 'In Progress';
        this.callHistory.push({ number, status: 'In Progress' });
      });

      session.on('terminated', () => {
        this.callStatus = 'Ended';
        this.callHistory.push({ number, status: 'Ended' });
      });
    },
    endCall() {
      if (this.sipUserAgent && this.sipUserAgent.activeSession) {
        this.sipUserAgent.activeSession.bye();
      }
    }
  },
  mounted() {
    this.initSIP();
  }
};
</script>
