<template>
    <div>
      <input type="text" v-model="phoneNumber" placeholder="Enter phone number" />
      <button @click="handleMakeCall">Call</button>
      <button @click="handleEndCall">End Call</button>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        phoneNumber: ''
      };
    },
    methods: {
      handleMakeCall() {
        this.$emit('makeCall', this.phoneNumber);
      },
      handleEndCall() {
        this.$emit('endCall');
      }
    }
  };
  </script>
  